.stars {
  display: flex;

  &__star {
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    width: $stars-size;
    height: $stars-size;
  }

  &__star:not(:last-child) {
    margin-right: 4px;
  }

  &--4 :nth-child(-n + 4) {
    background-image: url(../images/star-active.svg);
    background-repeat: no-repeat;
  }
}
